.porducts__strong {
    display: block;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;


    text-align: center;
    text-transform: uppercase;

    margin-top: 60px;

    color: $text-strong;

    @media screen and (min-width: 768px) {
        font-size: 26px;
        line-height: 30px;
    }

        @media screen and (min-width: 1200px) {
            font-size: 30px;
            line-height: 34px;
        }
}

.porducts__title {
    margin-top: 15px;
    margin-bottom: 8px;
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $text-primary;

    @media screen and (min-width: 768px) {
        font-size: 48px;
        line-height: 55px;
    }
        @media screen and (min-width: 1200px){
            font-size: 58px;
            line-height: 66px;
            margin-bottom: 20px;
        }

}

.porducts__list {
    @media screen and (min-width: 768px) {
        display: flex;
        margin: -8px;
    }

        @media screen and (min-width: 1200px){
            
            margin: -16px;
        }
}

.porducts__item {
    position: relative;
    margin-left: auto;
    margin-right: auto;

    width: 280px;

    @media screen and (min-width: 768px) {
        width: calc((100% - 48px)/3);
        margin: 8px;
    }

    @media screen and (min-width: 1200px) {
        width: calc((100% - 96px)/3);
        margin: 16px;
    }
}



.porducts__image {
    margin-bottom: -50%;

    @media screen and (min-width: 768px) {
        margin-bottom: -86px;
    }

    @media screen and (min-width: 1200px) {
        width: 100%;
        margin-bottom: -133px;

    }
}

.porducts-box {
    display: block;
    padding: 140px 0 32px 0;

    border-radius: 24px;

    &__cream{
        background: $header-bg;
    }
    &__cofe {
        background: $bg-cofe;
    }
    &__milkshakes {
        background: $color-olive;
    }
        @media screen and (min-width: 768px) {
            padding: 118px 0 40px 0;
        }
                @media screen and (min-width: 1200px){
                    padding: 165px 0 60px 0;

                }
}

.porducts-box__title {
    font-family: 'Titan One';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;

    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;

    color: $text-white; 
    
    margin-top: 20px;
    margin-bottom: 0;

    @media screen and (min-width: 768px){
        margin-top: 0;
    }
        @media screen and (min-width: 1200px){
            font-size: 30px;
            margin-top: 0; 
        }
        
}
.porducts-box__icon {
    display: flex;
    margin: 32px auto;
}


.products-box__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.5;
    text-align: center;
    letter-spacing: 0.04em;

    margin-bottom: 32px;

    color: $text-white;

    margin: 0 auto;

    @media screen and (min-width: 768px) {
        line-height: 1.76;
        width: 160px;
    }
        @media screen and (min-width: 1200px){
            font-size: 16px;
            line-height: 1.76;
            width: 245px;
        }

}

.products-button{
    display: flex;
    border-radius: 50%;
    background: $text-white;
    fill: $text-primary;
    border: none;
    padding: 15px;
    margin-top: 32px;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 1200px) {
        margin-top: 60px;
    }
}


